import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state={
    // introduction:[
    //     {id:1,name:'英雄联盟 LOL',intr:'这是英雄联盟的介绍',src:require("../../images/评论区页面/lol.jpg")},
    //     {id:3,name:'QQ飞车',intr:'这是QQ飞车的介绍',src:require("../../images/评论区页面/QQ飞车.jpg")},
    //     {id:168332,name:'原神',intr:'这是原神的介绍',src:require("../../images/评论区页面/原神.jpg")}
    // ],
    
    // font得出的评论总条数
    sum:88,
    //select获取后端数据，点击哪个游戏，就存哪个游戏到这来，用在top中。
    topgame:{gameid:'',gamename:'',gameintr:'',gamesrc:''}
}

const mutations= {
    //跳到评论页、把游戏信息存进state
    goevaluation(state,item) { 
        state.topgame.gameid=item.id;
        state.topgame.gamename=item.name;
        state.topgame.gameintr=item.intr;
        state.topgame.gamesrc=item.src;
    },
    getsession(state, game) {
        state.topgame.gameid= game. gameid;
        state.topgame.gameintr= game. gameintr;
        state.topgame.gamesrc= game. gamesrc;
        state.topgame.gamename= game. gamename;
        // 只能传一个参数
        
    },
    vvv(state,v){
        v=state.topgame.gameid;
        return v
    }
}

export default new Vuex.Store({
    state,mutations
})


