<template>
    <div class="login">
        <!--返回-->
        <i class="el-icon-back" @click="aa()" ></i>
        <p style="margin-top:0px;text-align:center;font-size:40px;">欢迎登录</p>
        <div class="fform">
            <el-form  :model="userform">
                <p>账号</p>
                <el-input v-model="userform.name"></el-input>
                <p>密码</p>
                <el-input v-model="userform.password" type="password" ></el-input>
                <p>验证码</p>
                <el-input v-model="userform.yzm"></el-input>
                
                <el-button type="primary" >登录</el-button>&nbsp&nbsp&nbsp
                <el-button>注册</el-button>
            </el-form>
        </div>
        <el-divider>继续即表示你同意
                 <a href="https://segmentfault.com/tos">《服务条款》</a>
                 和
                 <a href="https://segmentfault.com/n/1330000020312445">《隐私政策》</a>
        </el-divider>
       
    </div>
</template>

<script>
export default {
    data() {
        return{
            userform:{name:'',password:'',yzm:''},
            id:1
        }
    },
    methods:{
        aa(){
            this.$router.go(-1);
        }
    }
}
</script>

<style scoped>
.login {
    height: 100%;
    width: 100%;
}
.el-icon-back {
    position: absolute;
    left:15px;
    font-size: 40px;

}
p {
    text-align:left;
    font-size:20px;
}
.fform {
    margin:0px auto;
    width:600px;
}
.el-button {
    margin-top: 50px;
}
.el-divider {
    position: relative;
    top: 140px;
}


</style>


