<template>
    <div class="font">
        <el-row>
            <!-- 评论区 -->
            <el-col :span="17">
                <div v-for="item in fontlist" :key="item.id" class="fontcontent">
                    <h3 class="h33" >
                        <i class="el-icon-user" style="color: #999;font-size:15px"></i>
                        <span  style="color: #999;font-size:15px">{{item.name}}</span>
                        <el-rate v-model="item.star" :colors="colors" disabled style="display:inline;">
                        </el-rate>
                        <span class="time">2018-09-23</span>
                    </h3>
                    <p style="text-align:left;margin-left:25px;font-weight:normal">{{item.content}}</p>
                </div>
            </el-col>
            <!-- 功能键区 -->
            <el-col :span="7">
                 <div class="right">
                    <h3>共筛选出 {{ sum }} 条信息</h3>
                        <div style="" class="title1">
                            <i class="el-icon-price-tag"><span class="span1"> 单 选</span></i>
                        </div>
                    <!--单选框-->
                        <el-radio v-model="radio" label="所有评价" style="display:block;margin-top:10px;">所有评价</el-radio>
                        <el-radio v-model="radio" label="所有好评" style="display:block;">所有好评</el-radio>
                        <el-radio v-model="radio" label="所有差评" style="display:block;">所有差评</el-radio>
                        <el-radio v-model="radio" label="长篇好评" style="display:block;">长篇好评</el-radio>
                        <el-radio v-model="radio" label="长篇差评" style="display:block;margin-bottom:25px;">长篇差评</el-radio>
                        <div  class="title1">
                            <i class="el-icon-price-tag"><span class="span1"> 多 选</span></i>
                        </div>
                    <!--多选框,选中既压入数组-->
                        <el-checkbox-group v-model="checkList" >
                        <el-checkbox  label="VIP 用户"  style="display:block;margin-top:10px;"></el-checkbox>
                        <el-checkbox  label="路人评价"  style="display:block;"></el-checkbox>
                        <el-checkbox  label="带图评价"  style="display:block;"></el-checkbox>
                        <el-checkbox  label="最近30天" style="display:block;margin-bottom:25px;"></el-checkbox>
                        </el-checkbox-group>
                    <div style="border-top:1px dotted #e4e6eb;border-bottom:1px dotted #e4e6eb;"><h3>数据分析</h3></div>
                        <el-button type="primary" style="margin-top:25px" >
                            <i class="el-icon-s-data" @click="$router.push('/analyse')">&nbsp&nbsp口碑分析&nbsp》》</i>
                        </el-button>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import axios from 'axios'
import store from '../vuex/store'
import {mapState} from 'vuex'

export default {
    data() {
        return {
            fontlist:[],
            radio:'所有评价',
            checkList:[],
            //得出的评论总条数
            sum:11108,
            //评星的颜色
            colors: ['#99A9BF', '#F7BA2A', '#FF9900'],
        }
    },
    store,
    computed:mapState(['topgame']),
    methods: {
    },
    
    created:function() {
         // 请求前端本地数据
        // let url='../../static/'+this.topgame.gameid+'/a.json';
         // 请求后台数据
        let url='/game/'+this.topgame.gameid+'/main';
       

        axios.get(url)
        //成功
        .then(response=>{
            // console.log(response);
            alert(response.data.message);
            this.fontlist=response.data.data.friend;
        })
        //失败
         .catch()

    }
 
    
    
       
}
</script>

<style scoped>
.font {
    background-color:#fff;
}
.fontcontent {
    border-bottom:1px dotted #e4e6eb;
    border-right:1px dotted #e4e6eb;
}
.fontcontent:hover {
    background-color:#f9faff;
}
.time {
    float:right;
    font-size: 12px;
    color: #999;
    padding-right: 5px;
}
.h33 {
     text-align:left;
     margin:0px;
     font-weight:normal;
     padding-top: 10px;
     padding-left:10px;
}

/* 功能区 */
.title1 {
    background-color:white;
    width:100%;
    border-top: 1px dotted #e4e6eb;
    
}
.right {
    height: 800px;
    background-color:white;
}
/* 单选/多选 */
.span1 {
    font-size: 18px;
    font-weight: 600;
    line-height: 49px;
    color:#4b4b4b;
}
hr {
    background-color:rgb(233, 210, 210);
    border: none;
    height: 1px;
    margin: 20px;
}
.el-radio {
    margin-top: 25px;
    margin-right: 10px;
}
.el-checkbox   {
    margin-top: 25px;
    margin-right: 10px;
}
</style>


