<template>
    <!-- 这是展示图表的 -->
    <div  id="main" style="width:100%; height:90%;"></div>
</template>

<script>
// import store from '../vuex/store'
import $ from 'jquery'


// 引入echarts基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
// 引入线图组件
require('echarts/lib/chart/line')
// 引入柱饼图组件
require('echarts/lib/chart/pie')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require('echarts/lib/component/datazoom')


export default {
    data() {
        return {
             msg: 'Welcome to Your Vue.js App'
        }
    },
    // store
    mounted() {
        this.aa();
    },
    methods:{
        aa() {
            
            let myChart = echarts.init(document.getElementById('main'));
            myChart.setOption({
                title: {
                    text: '单日评分图',
                    left:0,
                    top:0
                },
                
                tooltip:{trigger:'axis',alwaysShowContent:true},
                    
                legend: {
                    data:['一星','二星','三星','四星','五星'],
                    top:10
                },
                xAxis: {
                    //boundaryGap: false,//坐标轴两边留白策略
                    name:'时间',
                    data: []
                },
                yAxis: {
                    name:'人数',
                    nameGap:5,//坐标轴名称与轴线之间的距离
                    //max:10
                },
                
                series: [
                    {	name: '一星',
                        type: 'line',
                        smooth:true,//是否平滑曲线
                        data: []
                    },
                    
                    {	name: '二星',
                        type: 'line',
                        smooth:true,//是否平滑曲线
                        data: []
                    },
                    
                    {	name: '三星',
                        type: 'line',
                        smooth:true,//是否平滑曲线
                        data: []
                    },
                    
                    {	name: '四星',
                        type: 'line',
                        smooth:true,//是否平滑曲线
                        data: []
                    },
                    
                    {	name: '五星',
                        type: 'line',
                        smooth:true,//是否平滑曲线
                        data: []
                    },
                    
                    {
                        type: 'pie',
                        id: 'pie',
                        radius: '30%',
                        center: ['80%', '25%'],//[x,y]
                        
                        label: {formatter: '{b}: {@0} ({d}%)'},
                        encode: 
                        {
                            itemName: 'time',
                            value: '0',
                            tooltip: '0'
                        },
                    }
                    
                ],
                dataZoom:[//用于区域缩放的组件
                        {
                            type:'slider',
                            start:0,
                            end:25
                        },
                        {
                            type:'inside',
                        }
                    ],
                });
		
                var one = new Array;
                var two = new Array;
                var three = new Array;
                var four = new Array;
                var five = new Array;
                var time = new Array;
              
                $(document).ready(function(){
                        $.getJSON("../../static/star.json",function(data){
                                //alert("Data: " + data.Data[0].time + "\nStatus: " + status);
                                //alert(data.Data.length);
                                
                                one[0] = "one";
                                two[0] = "two";
                                three[0] = "three";
                                four[0] = "four";
                                five[0] = "five";
                                time[0] = "time";
                                
                                for(let i=1; i<data.data.length+1; i++)
                                {
                                    one[i] = data.data[i-1].oneStarNum;
                                    two[i] = data.data[i-1].towStarNum;
                                    three[i] = data.data[i-1].threeStarNum;
                                    four[i] = data.data[i-1].fourStarNum;
                                    five[i] = data.data[i-1].fiveStarNum;
                                    time[i] = data.data[i-1].time.substring(0,10);//获取字符串从0开始10个字符
                                }
                                //alert("Data: " + number[0] + "\nTime: " + time[2]);
                                //alert(time[0]);
                                myChart.setOption
                                ({
                                    xAxis: {
                                        data: time
                                    },
                                    
                                    dataset:
                                    {
                                        
                                        source: 
                                        [
                                            time,
                                            one,
                                            two,
                                            three,
                                            four,
                                            five
                                        ]
                                    },
                                    
                                    series: 
                                    [
                                        {	name: '一星',
                                            data: one},
                                            
                                        {	name: '二星',
                                            data: two},
                                            
                                        {	name: '三星',
                                            data: three},
                                            
                                        {	name: '四星',
                                            data: four},
                                            
                                        {	name: '五星',
                                            data: five},
                                        
                                        
                                    ]
                                });
                                
                            });
                    });
                    myChart.on('updateAxisPointer', function (event) {
                    var xAxisInfo = event.axesInfo[0];
                    if (xAxisInfo) {
                        var dimension = xAxisInfo.value + 1;
                        myChart.setOption({
                            series: {
                                id: 'pie',
                                label: {
                                    formatter: '{b}: {@[' + dimension + ']} ({d}%)'
                                },
                                encode: {
                                    value: dimension,
                                    tooltip: dimension
                                }
                            }
                        });
                    }
                });
               // myChart.setOption(option);


            // aa()这里结束
        }
    }
}
</script>

<style scoped>
.analyse {
    height: 90%;
    width: 100%;
}
</style>


