<template>
    <div class="navigation">
         <el-row>
            <el-col :span="6">
                <p style="margin:0px;font-size:45px;margin-top:3px;font-weight:800;">评论智能筛选系统</p>
            </el-col>
            
            <!--主页 "$emit('goindex')-->
            <el-col :span="1" >
                <i class="el-icon-s-home" @click="$router.replace('/')" ></i>
            </el-col>

            <el-col :span="1">
            <i class="el-icon-s-grid" ></i>
            </el-col>

            <!--输入框-->
            <el-col :span="8">
            <el-input placeholder="请输入想查询的游戏" style="margin-top: 15px;" v-model="inputfont"></el-input>
            </el-col>

            <!--搜索bt-->
            <el-col :span="2">
                <el-button type="primary" icon="el-icon-search" @click="$router.push('/select1')" style="margin-top: 15px;">
                    搜索
                </el-button>
            </el-col>
            <el-col :span="1">&nbsp</el-col>

            <!--头像  goperson头像没写-->
            <el-col :span="1" >
                <el-avatar size="medium" :src="circleUrl" @click.native="$router.push('/person')" style="margin:0;margin-top:15px;"></el-avatar>
            </el-col>

            <!--登录--> 
            <el-col :span="1">
            <!-- 进行路由跳转，触发是为了在显示搜索页时候，全显示登录-->
            <router-link to="/login" ><p @click="$router.push('/login')" style="font-size:20px;margin:0px;margin-top:19px;">登录</p></router-link>
            </el-col>
            <!--注册--> 
            <el-col :span="1">
            <router-link to="/register" ><p @click="$router.push('/register')" style="font-size:20px;margin:0px;margin-top:19px;">注册</p></router-link>
            </el-col>
            <el-col :span="1">&nbsp</el-col>
            <el-col :span="1">
                <i class="el-icon-s-data" @click="$router.push('/analyse')"></i>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
        //头像
        circleUrl:'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
        inputfont:''
        }
    },
    methods: {

    }
}
</script>

<style scoped>
  .navigation {
    height:10%;
    width: 100%;
    background-color:#777777;
    margin: 0px;
    }
  i {
    margin-top: 15px;
    font-size: 40px;
    }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


