<template>
<!--题目-->
    <div class="person">
        <div style="height:70px;width:100%;background-color:#DCDFE6;"><p class="GRXX">个人信息页</p></div>
    

    
<!--头像上传-->
        <p style="text-align:left;margin-left: 30px;font-size:30px;">头像</p>
        <el-upload style="text-align:left"
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <p style="text-align:left;margin-left: 30px;font-size:30px;">名称</p>
<!--名称-->
        <div style="width:500px;"><el-input v-model="personname"></el-input></div>
        <p style="text-align:left;margin-left: 30px;font-size:30px;">关注的游戏</p>
<!--关注的游戏-->
这里弄个抽屉出来把
    </div>
</template>

<script>
export default {
    data() {
        return{
             imageUrl: '',
             personname:''
        }
    },
     methods: {
         //头像
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
    }
  }
</script>

<style scoped>
.person {
  height:90%;
  width:100%;
  background-color:#F2F6FC;
}
.GRXX {
    font-size: 40px;
    margin-top: 0px;
    margin-bottom: 0px;
    width: 30%;
}
.el-input {
    margin-left: 80px;
}
/*上传头像的 */
.avatar-uploader  {
    margin-left: 80px;
    border: 1px dashed #0f0202;
    width: 178px;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar{
    border: 1px dashed #0f0202;
    width: 178px;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
 
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


