import Vue from 'vue'
import Router from 'vue-router'
import index1 from '@/components/index1'
import login from '@/components/login'
import evaluation from '@/components/evaluation'
import person from '@/components/person'
import { Select } from 'element-ui'
import select1 from '@/components2/select1'
import analyse from '@/components2/analyse'
import register from '@/components/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index1',
      component:index1
      },
      {
        path:'/login',
        name:'login',
        component:login
      },
      {
        path:'/evaluation',
        name:'evaluation',
        component:evaluation
      },
      {
        path:'/select1',
        name:'select1',
        component:select1
      },
      {
        path:'/person',
        name:'person',
        component:person
      },
      {
        path:'/analyse',
        name:'analyse',
        component:analyse
      },
      {
        path:'/register',
        name:'register',
        component:register
      }
  ]
})



