<template>
    <div class="select1">
        <el-row>
            <el-col :span="4">&nbsp</el-col>
            <!-- 主要展示区 -->
            <el-col :span="16" style="background-color:white;">
                <div  v-for="item in game" :key="item.id" class="box" @click="aa(item)">
                    
                    <!-- 图片--{{ item.name }}---游戏id{{item.id}}--获取的id{{gameid}} -->
                    <p class="span">{{item.name}}</p>
                    <!-- {{sum}}--{{iid}}--{{bb}}        测试代码 -->
                    <!-- <el-button class="button" ><span style="text-align:center;">关&nbsp&nbsp&nbsp注</span></el-button> -->
                </div>
            </el-col>
            <el-col :span="4">&nbsp</el-col>
        </el-row>
    </div>
</template>

<script>
import store from '@/vuex/store'
import {mapState} from 'vuex'

export default {
    data() {
        return{
            //这是写死的，需要后端传
            game:[
                {id:1,name:'英雄联盟 LOL',intr:'这是英雄联盟的介绍',src:require("../../images/评论区页面/lol.jpg")},
                {id:3,name:'QQ飞车',intr:'这是QQ飞车的介绍',src:require("../../images/评论区页面/QQ飞车.jpg")},
                {id:168332,name:'原神',intr:'这是原神的介绍',src:require("../../images/评论区页面/原神.jpg")}
            ],
            //测试变量
            // iid:0,
            // bb:99
        }
    },
    methods:{
        //点击游戏触发事件
        aa(item) {

            //把该游戏的id存到仓库里。成功
            this.$store.commit('goevaluation', item);
            //把数据存到sessionstorage
            sessionStorage.setItem('name',item.name);
            sessionStorage.setItem('intr',item.intr);
            sessionStorage.setItem('src',item.src);
            sessionStorage.setItem('id',item.id);
            // 跳转到评论页
            this.$router.push('/evaluation');
            //测试computed属性
            // this.iid=this.sum;
            // this.bb=this.topgame.gameid;


        }
    },
    //可以直接把下面值当data来用
    computed:mapState(['sum','topgame']),
    store
    
}
</script>

<style scoped>
.box {
    height: 100px;
    width: 100%;
    border-bottom:1px dotted #e4e6eb;
    display: flex;
}
/* .box:hover { background-color: rgb(235, 221, 217); } */
.box:hover { background-image: linear-gradient(to right,rgb(201, 224, 247), white);}
/* .box:hover>p { font-size: 30px;color:rgb(214, 74, 39);font-weight: 800;} */
.box:hover>p { font-size: 30px;color:#409EFF;font-weight: 800;}
.select1 {
    background-color: cadetblue;
    height:90%;
    width: 100%;
}
.span {
    font-family: Microsoft YaHei;
    color: rgb(151, 144, 144);
    font-size: 20px;
    margin: auto 25px;
}
.button {
    height:42px;
    width: 80px;
    /* 上下自动，左右25px */
    margin: auto  25px;
    margin-left: auto;
    display: flex;
    justify-content:flex-end;
}
</style>


