<template>
    <div class="top">
        <img :src="this.topgame.gamesrc" class="logo">
        <div class="font">
            <P class="gamename">{{topgame.gamename}}</P>
            <p  class="intr">
                {{topgame.gameintr}}
            </p>
            <div class="button1">
                <el-button type="primary" @click="msg()">关注</el-button>
                <el-button type="primary"  style="margin-left:22px;">
                    <i class="el-icon-s-data" @click="$router.push('/analyse')">&nbsp&nbsp口碑分析&nbsp</i>
                </el-button>
            </div>
        </div>
    </div>
</template>

<script>
import store from '@/vuex/store'
import {mapState} from 'vuex'
 

export default {
    data() {
        return {
        }
    }, 
    computed:mapState(['topgame']),
    store,
    methods: {
        msg() {
          this.$notify({
          title: '成功',
          message: '已成功添加至关注',
          type: 'success'
        });
        }
    },
    created() {
        if(this.topgame.gameid=='')
        {
            // console.log("2");
            //    页面刷新后，vuex的值清空，,data还没挂载前，把session重新赋值给vuex
            var game = {gameid:'',gamename:'',gameintr:'',gamesrc:''}
            game.gameid = sessionStorage.getItem('id');
            // console.log(game.gameid);
            game.gamename = sessionStorage.getItem('name');
            // console.log(game.gamename);
            game.gameintr = sessionStorage.getItem('intr');
            // console.log(game.gameintr);
            game.gamesrc = sessionStorage.getItem('src');
            // console.log(game.gamesrc);
            this.$store.commit('getsession', game);
            // console.log("3");
        }
        // console.log(this.topgame.gameid); //成功
        // console.log(this.topgame.gamename); //成功
        // console.log(this.topgame.gameintr); //成功
        // console.log(this.topgame.gamesrc); //成功
        // 只能传一个参数
    }
}
</script>

<style scoped>
.top {
    height: 300px;
    width: 100%;
    background-color: white;
    display:flex;
    /* 起点在左端，往右排 */
    flex-direction:row;
    border-bottom:1px dotted #e4e6eb;
}
.font {
    display:flex;
    /* 起点在shang端，往下排 */
    flex-direction:column;
    flex-basis:auto; 
    width: 100%;
}
.intr {
    text-align:left;
    display:flex;
    flex-grow: 13;
}
.button1 {
    display:flex;
    /* 起点在左端，往右排 */
    flex-direction:row;
    text-align:right;
    align-self: flex-end;
    flex-grow: 1;
    padding-right: 20px;
    padding-bottom: 20px;
}
.gamename {
    font-size:50px;
    margin-bottom: 10px;
    margin-top: 0px;
    text-align: center;
    flex-grow: 0;
    font-weight: 600;
    color: #5f5f5f;
}
.logo {
    height: 260px;
    float: left;
    margin: 12px;
}


</style>


